"use client";
import React, { useState } from "react";
import { db, auth } from "../../../firebase/firebase";
import { collection, addDoc, doc, setDoc } from "firebase/firestore";
import { createUserWithEmailAndPassword } from "firebase/auth";

export default function CreateSuperAdmin() {
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        password: '',
        confirmPassword: '',
        role: 'superadmin'
    });
    const [loading, setLoading] = useState(false);
    const [message, setMessage] = useState('');

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!formData.name || !formData.email || !formData.password) {
            setMessage('Vui lòng nhập đầy đủ thông tin!');
            return;
        }

        if (formData.password !== formData.confirmPassword) {
            setMessage('Mật khẩu xác nhận không khớp!');
            return;
        }

        if (formData.password.length < 6) {
            setMessage('Mật khẩu phải có ít nhất 6 ký tự!');
            return;
        }

        setLoading(true);
        try {
            // Bước 1: Tạo tài khoản Firebase Auth
            const userCredential = await createUserWithEmailAndPassword(
                auth,
                formData.email,
                formData.password
            );

            const user = userCredential.user;

            // Bước 2: Lưu thông tin admin vào Firestore
            const adminData = {
                name: formData.name,
                email: formData.email,
                role: formData.role,
                createdAt: new Date(),
                uid: user.uid
            };

            // Lưu vào collection adminUsers
            await setDoc(doc(db, "adminUsers", user.uid), adminData);

            setMessage('Tạo Super Admin thành công! Bạn có thể đăng nhập với email và mật khẩu vừa tạo.');
            setFormData({
                name: '',
                email: '',
                password: '',
                confirmPassword: '',
                role: 'superadmin'
            });
        } catch (error) {
            console.error("Error creating admin:", error);
            if (error.code === 'auth/email-already-in-use') {
                setMessage('Email này đã được sử dụng!');
            } else if (error.code === 'auth/weak-password') {
                setMessage('Mật khẩu quá yếu!');
            } else {
                setMessage('Lỗi khi tạo Super Admin: ' + error.message);
            }
        }
        setLoading(false);
    };

    return (
        <div className="min-h-screen bg-gray-100 flex items-center justify-center">
            <div className="bg-white p-8 rounded-lg shadow-lg w-full max-w-md">
                <h1 className="text-2xl font-bold text-center mb-6 text-red-600">
                    ⚠️ Tạo Super Admin
                </h1>

                <div className="bg-yellow-100 border border-yellow-400 text-yellow-700 px-4 py-3 rounded mb-4">
                    <strong>Lưu ý:</strong> Trang này tạo tài khoản đăng nhập hoàn chỉnh.
                    Sau khi tạo xong, hãy xóa trang này để bảo mật.
                </div>

                <form onSubmit={handleSubmit} className="space-y-4">
                    <div>
                        <label className="block text-gray-700 mb-2">Tên Super Admin</label>
                        <input
                            type="text"
                            className="w-full border border-gray-300 rounded px-3 py-2 focus:outline-none focus:border-blue-500"
                            value={formData.name}
                            onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                            placeholder="Nhập tên Super Admin"
                        />
                    </div>

                    <div>
                        <label className="block text-gray-700 mb-2">Email (dùng để đăng nhập)</label>
                        <input
                            type="email"
                            className="w-full border border-gray-300 rounded px-3 py-2 focus:outline-none focus:border-blue-500"
                            value={formData.email}
                            onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                            placeholder="Nhập email"
                        />
                    </div>

                    <div>
                        <label className="block text-gray-700 mb-2">Mật khẩu</label>
                        <input
                            type="password"
                            className="w-full border border-gray-300 rounded px-3 py-2 focus:outline-none focus:border-blue-500"
                            value={formData.password}
                            onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                            placeholder="Nhập mật khẩu (ít nhất 6 ký tự)"
                        />
                    </div>

                    <div>
                        <label className="block text-gray-700 mb-2">Xác nhận mật khẩu</label>
                        <input
                            type="password"
                            className="w-full border border-gray-300 rounded px-3 py-2 focus:outline-none focus:border-blue-500"
                            value={formData.confirmPassword}
                            onChange={(e) => setFormData({ ...formData, confirmPassword: e.target.value })}
                            placeholder="Nhập lại mật khẩu"
                        />
                    </div>

                    <div>
                        <label className="block text-gray-700 mb-2">Vai trò</label>
                        <select
                            className="w-full border border-gray-300 rounded px-3 py-2 focus:outline-none focus:border-blue-500"
                            value={formData.role}
                            onChange={(e) => setFormData({ ...formData, role: e.target.value })}
                        >
                            <option value="superadmin">Super Admin</option>
                            <option value="admin">Admin</option>
                            <option value="moderator">Moderator</option>
                        </select>
                    </div>

                    <button
                        type="submit"
                        disabled={loading}
                        className="w-full bg-red-600 text-white py-2 px-4 rounded hover:bg-red-700 disabled:opacity-50"
                    >
                        {loading ? 'Đang tạo...' : 'Tạo Admin'}
                    </button>
                </form>

                {message && (
                    <div className={`mt-4 p-3 rounded ${message.includes('thành công')
                            ? 'bg-green-100 text-green-700 border border-green-400'
                            : 'bg-red-100 text-red-700 border border-red-400'
                        }`}>
                        {message}
                    </div>
                )}

                <div className="mt-6 text-center">
                    <a href="/admin" className="text-blue-600 hover:underline">
                        ← Quay lại Admin Panel
                    </a>
                </div>
            </div>
        </div>
    );
}